	module.exports = {"data":{"site":{"siteMetadata":{"title":"Justin Ball","author":"Justin Ball"}},"markdownRemark":{"id":"/Users/jbasdf/projects/justinball/src/posts/2011-03-15-babelphish-is-a-little-less-rotten/index.md absPath of file >>> MarkdownRemark","html":"<p>I've let Babelphish sit for a while. It did what it was supposed so why change it? Then Michael Jenik offered some changes that made it compatible with the latest Google Translate API. To work with the latest API you have to get a key, but it's pretty easy to do and babelphish will look for it in your home directory. <a href=\"https://github.com/jbasdf/babelphish\">Check out the readme on github for the details</a> and let me know if there are bugs. If you have any questions the answer is 42. Happy translating!</p>","frontmatter":{"title":"Babelphish is a Little Less Rotten","date":"March 15, 2011","imageUrl":null,"structuredHtml":null,"image":null}}},"pathContext":{"path":"/babelphish-is-a-little-less-rotten"}}

